
@import "variables";

.item {
  box-sizing: border-box;
  display: grid;
  align-items: center;
  height: 403px;
  width: $item-conteiner-width;
  line-height: 13px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 15px;

  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
  }

  &__pic-container {
    display: grid;
    align-items: center;
    justify-content: center;
    height: 164px;
  }

  &__img {
    padding: 12px 0;
    width: 160px;
    height: 134px;
    transition: transform 1s;

    &:hover {
      transform: scale(1.2);
      border: 3px solid #f3f3f3;
      border-radius: 5px;
    }
  }

  &__title {
    margin: 0;
    padding: 0;
    margin-left: -1px;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    margin-top: 30px;
  }

  &__code {
    margin: 5px 0;
  }

  &__price {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0;

    &--value {
      font-weight: bold;
      font-size: 16px;
      margin: 13px 0;
    }
  }

  &__buy-button {
    box-sizing: border-box;
    display: grid;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 40px;
    width: 170px;
    margin-top: 10px;
    text-decoration: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #fff;
    background: $primary-light-blue;
    border: 1px solid $primary-light-blue;
    border-radius: 5px;
    transition: 0.5s;

    &:hover {
      color: $primary-light-blue;
      background: #fff;
    }
  }
}

.rating {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  grid-column-gap: 17px;
  align-items: center;
  margin: 0;

  &__stars {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }

  &__star {
    height: 15px;
    width: 15px;
    margin-right: 4px;
    background-image: url(../images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  &__star:nth-child(-n+4) {
    background-image: url(../images/star-active.svg);
  }
}
